import React from 'react';
import { ButtonsContainer, ButtonsHeader, Button } from "./styled";

const Buttons = ({ tasks, hideDone, toggleHideDone, setAllDone }) => {
  if (tasks.length === 0) {
    return (
      <ButtonsContainer>
        <ButtonsHeader>
          Lista zada≈Ñ
        </ButtonsHeader>
        <Button none disabled></Button>
        <Button none disabled></Button>
      </ButtonsContainer>
    )
  }
  return (
    <ButtonsContainer>
      <ButtonsHeader>
        Lista zada≈Ñ
        </ButtonsHeader>
      <Button
        onClick={toggleHideDone}
      >
        {hideDone ? "Poka≈º" : "Ukryj"} uko≈Ñczone
      </Button>
      <Button
        disabled={tasks.every(({ done }) => done)}
        onClick={setAllDone}
      >
        Uko≈Ñcz wszystkie
      </Button>
    </ButtonsContainer>
  )
};

export default Buttons;
